.newProduct{
    flex:4;
    margin-left:40%;
    margin-top: 2rem;
    
}

.addProductForm{
    margin-top:10px;
}
.addProductItem{
    width: 250px;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;

}

.addProductItem > label{
    color: gray;
    font-weight: 600;
    margin-bottom: 10px;
}
.addProductItem > input{
    padding: 10px;
}

.addProductItem > select{
    padding: 10px;
}

.addProductButton{
    margin-top: 10px;
    margin-left: 5rem;
    padding: 7px 10px;
    border: none;
    border-radius: 10px;
    background-color: darkblue;
    color: white;
    font-weight: 600;
    cursor: pointer;
}